// ğŸ”¤ ì—…ë¡œë“œ ì‹œ ë‹¤êµ­ì–´ í•„ë“œ ê¸°ë³¸ êµ¬ì¡° ìƒì„±
function buildMultilingualText(row) {
  const base = (row.en || "").trim();

  // ì•ìœ¼ë¡œ AI ë²ˆì—­ APIë¥¼ ë¶™ì¼ ìë¦¬ëŠ” ì—¬ê¸°ì…ë‹ˆë‹¤.
  // ì§€ê¸ˆì€ CSVì— ko/zh/kk/bn/uzê°€ ë¹„ì–´ ìˆìœ¼ë©´
  // ì¼ë‹¨ ì˜ì–´ ë¬¸ì¥ì„ ê·¸ëŒ€ë¡œ ë³µì‚¬í•´ ë‘¡ë‹ˆë‹¤.
  return {
    en: base,
    ko: (row.ko || base).trim(),
    zh: (row.zh || base).trim(),
    kk: (row.kk || base).trim(),
    bn: (row.bn || base).trim(),
    uz: (row.uz || base).trim(),
  };
}

